@import "vars";
@import "mixins/burger";
@import "mixins/breakpoint";

.header {
  background-color: $black;
  padding-top: 22px;
  align-items: center;

  @include tablet {
    padding-bottom: 27px;
  }
}

.logo {
  height: 51px;

  &__img {
    width: 50px;
    height: 51px;
    object-fit: cover;
    object-position: center;
  }
}

.menu {
  display: flex;
  justify-content: space-between;

  @include tablet {
    align-items: center;
  }

  &__list {
    display: flex;
    transition: opacity 0.5s;

    @include tablet {
      opacity: 0;
      position: absolute;
      z-index: 1;
      top: 100px;
      left: 0;
      right: 0;
      flex-direction: column;
      background-color: $black;
    }

    &--active {
      opacity: 1;
    }
  }

  &__item {
    padding-top: 15px;

    &:not(:last-child) {
      margin-right: 4px;
    }

    @include tablet {
      padding-top: 0;
      margin-right: 0;
    }
  }

  &__link {
    display: inline-block;
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 14px;
    line-height: 1.3;
    color: $white;
    text-transform: uppercase;
    padding: 0 8px 38px 8px;
    border-bottom: 5px solid transparent;
    outline: transparent;
    transition: border-color 0.3s;

    @include desktop {
      &:hover {
        border-color: $accent;
      }
    }

    @include laptop {
      font-size: 12px;
    }

    @include tablet {
      display: block;
      padding: 20px 0;
      text-align: center;
      font-size: 14px;
    }

    &--active {
      border-color: $accent;
    }

    &:focus {
      color: $accent;
    }

    &:active {
      color: $accent;
    }
  }
}

.burger {
  @include burger;
  display: none;

  @include tablet {
    display: block;
  }
}

.hiro {
  position: relative;
  margin-bottom: 50px;
  height: 750px;
  background-color: $bg;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  text-align: center;

  @include tablet {
    height: 100vh;
  }

  &__slider-item {
    padding-bottom: 20px;
  }

  &__title {
    padding-top: 235px;
    font-weight: 700;
    font-size: 72px;
    line-height: 1;
    color: $white;
    margin-bottom: 10px;
    text-transform: uppercase;

    span {
      display: block;
      color: $accent;
      line-height: 1;
    }

    @include mobile {
      font-size: 48px;
      padding-top: 100px;
    }

    @include mobile-vertical {
      font-size: 48px;
      padding-top: 100px;
    }

    @include middle {
      font-size: 32px;
    }

    @include mini-vertical {
      padding-top: 50px;
    }
  }

  &__subtitle {
    display: block;
    font-weight: 600;
    font-size: 24px;
    color: $white;
    text-transform: uppercase;
    margin-bottom: 35px;

    @include middle {
      font-size: 20px;
    }
  }

  &__btn {
    width: 194px;
    height: 46px;
    line-height: 46px;
  }

  &__link {
    bottom: -33px;
    right: 245px;

    &::after {
      content: url(../img/telephone.png);
      position: absolute;
      width: 40px;
      height: 39px;
      right: 12px;
      top: 12px;
    }

    @include tablet {
      bottom: 20px;
      right: 50px;
    }
  }
}

.slick-dots {
  position: absolute;
  display: flex;
  justify-content: center;
  left: 0;
  right: 0;
  bottom: 40px;

  li {
    transform: translateX(-50%);

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  button {
    font-size: 0;
    padding: 0;
    height: 14px;
    width: 14px;
    background-color: transparent;
    border-radius: 50%;
    border: 2px solid $accent;
  }

  .slick-active {
    button {
      background-color: $accent;
    }
  }
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;

  &__window {
    margin: 0 10px;
    border-radius: 27px;
    background-color: white;
    max-width: 600px;
    width: 100%;
    position: relative;
    padding: 30px;
    text-align: center;
  }

  &__close {
    position: absolute;
    right: 10px;
    top: 0px;
    color: $black;
    font-size: 40px;
    font-weight: 400;
    cursor: pointer;
  }

  &__form-inner {
    margin-bottom: 50px;
  }

  &__form-label {
    display: block;
    margin-bottom: 20px;
  }

  &__form-input {
    font-family: 'Montserrat';
    font-size: 14px;
    line-height: 1.9;
    font-weight: 300;
    width: 100%;
    border: none;
    border-bottom: 2px solid $silver;
    padding: 10px 0px 5px 0px;
    outline: transparent;

    &:focus {
      border-bottom: 5px solid $silver;
    }

    &::placeholder {
      font-family: 'Montserrat';
      font-size: 13px;
      line-height: 1.9;
      font-weight: 300;
      text-transform: uppercase;
    }
  }

  &__form-btn {
    background-color: $accent;
    line-height: 45px;
    height: 45px;
    width: 194px;

    &:focus {
      box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
    }
  }
}



.about {
  margin-bottom: 80px;

  &__title {
    text-align: center;

  }

  &__subtitle {
    text-align: center;
    margin-bottom: 40px;
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__left {
    max-width: 636px;
    width: 100%;
    margin-bottom: 30px;
  }

  &__text {
    font-weight: 300;
    line-height: 1.8;
    margin-bottom: 35px;
  }

  &__btn {
    width: 195px;
    height: 46px;
    text-align: center;
    line-height: 46px;

    &:focus {
      box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
    }
  }

  &__video {
    display: block;
    background-color: $bg;
    box-shadow: 13px 0 24px 0 rgba(0, 0, 0, 0.42);
  }

  &__img {
    width: 445px;
    height: 275px;
    object-fit: cover;
    object-position: center;
  }
}

.price {
  background-color: $galery;
  padding: 40px 0;
  border-top: 1px solid $silver;
  border-bottom: 1px solid $silver;
  margin-bottom: 45px;

  &__title {
    text-align: center;
  }

  &__subtitle {
    text-align: center;
    margin-bottom: 40px;
  }

  &__list {
    display: flex;
    justify-content: space-between;

    @include laptop {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__item {
    margin-bottom: 20px;
    margin-right: 20px;
  }
}

.price-card {
  width: 255px;
  height: 345px;
  background-color: $white;
  border-radius: 3px;
  text-align: center;
  padding: 25px 10px 33px;

  &__title {
    font-family: 'Montserrat';
    font-size: 14px;
    line-height: 1.5;
    font-weight: 400;
    color: $text;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  &__num {
    font-family: 'Montserrat';
    font-size: 30px;
    line-height: 1;
    font-weight: 700;
    color: $text;
  }

  &__term {
    display: block;
    font-family: 'Montserrat';
    font-size: 10px;
    line-height: 1.5;
    font-weight: 400;
    color: $galery;
    text-transform: uppercase;
    margin-bottom: 18px;
  }

  &__list {
    margin-bottom: 15px;
  }

  &__item {
    position: relative;
    font-family: 'Montserrat';
    font-size: 10px;
    line-height: 1.5;
    font-weight: 400;
    color: $bg;
    padding-bottom: 6px;
    margin-bottom: 6px;

    &:not(:last-child) {

      &::after {
        content: '';
        position: absolute;
        width: 10px;
        height: 1px;
        background-color: $bg;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  &__btn {
    width: 130px;
    height: 45px;
    line-height: 45px;
    background-color: $bg;

    @include desktop {
      &:hover {
        background-color: $accent;
      }
    }

    &:focus {
      box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
    }
  }
}

.contacts {
  margin-bottom: 25px;

  &__title {
    text-align: center;
  }

  &__subtitle {
    text-align: center;
    margin-bottom: 40px;
  }

  &__form {
    margin: 0 auto;
  }
}

.form-message {
  max-width: 600px;
  width: 100%;
  padding: 30px;
  background-color: $white;
  text-align: center;
  position: relative;

  &__top {
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-wrap: wrap;
    }

    .form-message__lable {
      max-width: 260px;
      width: 100%;

      @include mobile {
        max-width: 600px;
      }
    }
  }

  &__lable {
    margin-bottom: 35px;
  }

  &__input,
  &__textarea {
    font-family: 'Montserrat';
    font-size: 14px;
    line-height: 1.9;
    font-weight: 300;
    width: 100%;
    border: none;
    border-bottom: 2px solid $silver;
    padding: 10px 0px 15px 0px;
    outline: transparent;

    &:focus {
      border-bottom: 5px solid $silver;
    }

    &::placeholder {
      font-family: 'Montserrat';
      font-size: 13px;
      line-height: 1.9;
      font-weight: 300;
      text-transform: uppercase;
    }
  }

  &__textarea {
    position: relative;
    resize: none;
    height: 110px;
    margin-bottom: 45px;

    &::placeholder {
      position: absolute;
      bottom: 5px;
    }
  }

  &__btn {
    background-color: $accent;
    line-height: 45px;
    height: 45px;
    width: 194px;

    &:focus {
      box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.75);
    }
  }
}

.footer {
  background-color: $black;
  padding: 30px 0 50px;
  position: relative;

  @include tablet {
    padding-top: 60px;
  }

  &__inner {
    display: flex;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__left {
    max-width: 275px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 110px;
  }

  &__bu {
    color: $white;
    text-transform: uppercase;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__text {
    font-weight: 300;
    font-size: 12px;
    line-height: 1.75;
    color: $silver;
    max-width: 350px;
  }

  &__link {
    top: -33px;
    left: 10%;

    &::after {
      content: url(../img/scrollup.png);
      position: absolute;
      width: 33px;
      height: 18px;
      right: 16px;
      top: 20px;
    }

    @media (max-width: (1460px)) {
      left: 80%;
    }

    @include mini {
      left: 250px;
    }
  }
}

.social {
  display: flex;
  margin-bottom: 20px;

  &__item {
    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    background-color: $bg;
    position: relative;
    outline: transparent;
    text-align: center;
    transition: background-color 0.3s;

    @include desktop {
      &:hover {
        background-color: $accent;
      }
    }

    &:focus {
      box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.75);
    }

    &:active {
      background-color: $accent;
    }
  }


}
