@mixin desktop {
  @media (min-width: (991px)) {
    @content;
  }
}

@mixin laptop {
  @media (max-width: (1199px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (991px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (767px)) {
    @content;
  }
}

@mixin mobile-vertical {
  @media (max-height: (575px)) {
    @content;
  }
}

@mixin middle {
  @media (max-width: (575px)) {
    @content;
  }
}

@mixin mini-vertical {
  @media (max-height: (360px)) {
    @content;
  }
}

@mixin mini {
  @media (max-width: (320px)) {
    @content;
  }
}
